@use 'base/variables' as v;
@use 'base/mixins' as mix;

.agenda {
    padding: 5rem 0;
    @include mix.contenedor();
}

.eventos {

    $modificador: &;

    &__heading {
        color: v.$azul;
        text-align: left;
        margin: 5rem 0;
        font-size: clamp(2rem, -0.8rem + 8vw , 6rem);
    }

    &__fecha {
        color: v.$gris;
        margin: 3rem 0 1rem 0;
    }

    &--workshops {
        #{$modificador}__heading {
            color: v.$secundario;
        }
    }
}